___CSS_LOADER_EXPORT___.push([module.id, `* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    user-select: none;
}

body {
    height: 100vh;
    width: 100vw;

    display: flex;
    justify-content: center;
    align-items: center;
}

.sliderContainer {
    display: flex;
    width: 600px;
    height: 600px;
    border: 3px solid black;
}

.imageFrame {
    flex: 1;
    border: 1px solid red;

    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: clip;
}

.imageDiv {
    flex: 1;
    position: absolute;
    opacity: 0;
}
.imageDiv[data-active="true"] {
    opacity: 1;
}`, "",{"version":3,"sources":["webpack://./src/styles.css"],"names":[],"mappings":"AAAA;IACI,SAAS;IACT,UAAU;IACV,sBAAsB;IACtB,iBAAiB;AACrB;;AAEA;IACI,aAAa;IACb,YAAY;;IAEZ,aAAa;IACb,uBAAuB;IACvB,mBAAmB;AACvB;;AAEA;IACI,aAAa;IACb,YAAY;IACZ,aAAa;IACb,uBAAuB;AAC3B;;AAEA;IACI,OAAO;IACP,qBAAqB;;IAErB,aAAa;IACb,uBAAuB;IACvB,mBAAmB;IACnB,gBAAgB;IAChB,mBAAmB;IACnB,mBAAmB;AACvB;;AAEA;IACI,OAAO;IACP,kBAAkB;IAClB,UAAU;AACd;AACA;IACI,UAAU;AACd","sourcesContent":["* {\r\n    margin: 0;\r\n    padding: 0;\r\n    box-sizing: border-box;\r\n    user-select: none;\r\n}\r\n\r\nbody {\r\n    height: 100vh;\r\n    width: 100vw;\r\n\r\n    display: flex;\r\n    justify-content: center;\r\n    align-items: center;\r\n}\r\n\r\n.sliderContainer {\r\n    display: flex;\r\n    width: 600px;\r\n    height: 600px;\r\n    border: 3px solid black;\r\n}\r\n\r\n.imageFrame {\r\n    flex: 1;\r\n    border: 1px solid red;\r\n\r\n    display: flex;\r\n    justify-content: center;\r\n    align-items: center;\r\n    overflow: hidden;\r\n    white-space: nowrap;\r\n    text-overflow: clip;\r\n}\r\n\r\n.imageDiv {\r\n    flex: 1;\r\n    position: absolute;\r\n    opacity: 0;\r\n}\r\n.imageDiv[data-active=\"true\"] {\r\n    opacity: 1;\r\n}"],"sourceRoot":""}]);
